body{
  background:#0c0c0c;
}
.nav{
  box-shadow: 2px 2px 5px 5px rgb(4 4 4);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.indexCenter{
  position: absolute;
  top: 50%;
  left: 50%;
}
img{
  width:100%;
}
.titleBox p{
  font-weight:bold;
}
.titleBox a{
  text-decoration: none;
  color:#fff;
}
.title{
  margin-bottom:0;
}
.titleBox a:hover .title{
  color:#777;
  transition:all 2s;
}
.titleBox a:hover img{
  opacity: 0.3;
  transition:all .5s;
}
.titleBox{
  position: relative;
}
.titleBox ul{
  left: 20px;
  padding-left: 0;
  position: absolute;
  top: 5px;
  opacity: 0;
}
.titleBox:hover .titleList{
  opacity: 1;
  transition: .3s all;
}
.bg-dark{
  background-color:#000;
}
.titleBox ul li{
  list-style-type: none;
  display: inline-block;
  background: #212529;
  padding: 5px 10px;
  border-radius: 25px;
  margin: 5px 5px 5px 0;
  color:#fff;
}
.detailbox h2{
  color:#fff;
}
.detailbox ul{
  padding-left:0;
}
.detailbox ul li{
  list-style-type: none;
  display: inline-block;
  background: #212529;
  padding: 5px 15px;
  border-radius: 25px;
  margin: 5px 5px 5px 0;
  color:#fff;
}
.detailbox span{
  margin-left:10px;
  font-size:1em;
}
.detailbox p{
  color:#fff;
}
.indexCenter{
  color:#fff;
}
.search-form button{
  min-width:70px;
}
.form-control{
  min-width:300px;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
